@import url(https://fonts.googleapis.com/css?family=Poppins:500&display=swap);
* {
    font-family: 'Poppins', sans-serif;
}
* {
    margin: 0;
    padding: 0;
}
.menuIcon {
    -webkit-transition: .1s;
    transition: .1s;
}

.menuIcon:hover {
    color: #212121;
}
.contextMenu {
    position: fixed;
    right: 2em;
    list-style: none;
    background-color: white;
    padding: 2em;
    box-shadow: 0 .9em 1.5em rgba(0,0,0,0.05);
    z-index: 10;
    top: 4.5em;
}

.contextMenu li {
    margin: 10px;
    color: #c9c9c9;
    padding: 10px;
    -webkit-transition: .1s;
    transition: .1s;
    cursor: pointer;
}

.contextMenu li:hover {
    color: #212121;
}
.like {
    -webkit-transition: .1s;
    transition: .1s;
}

.like:hover {
    color: #e41749;
}

.coloraReel {
    list-style: none;
    display: flex;
    position: absolute;
    margin: 1.5em;
}

.likesCounter {
    position: absolute;
    margin: 1.9em;
    right: 2em;
    color: rgba(158, 158, 158, 0.5);
    text-align: right;
}
.colorContainer {
    margin: .3em;
    overflow: hidden;
    display: flex;
    padding: .25em .5em .25em .5em;
    border-radius: 5em;
    width: 1em;
    -webkit-transition: .3s;
    transition: .3s;
    cursor: pointer;
}

.colorContainer:hover {
    width: 5.8em;
}

.colorContainer .colorShow {
    height: 1.05em;
    width: 1em;
    border-radius: 50%;
    margin-top: .25em;
    position: absolute;
}

.colorContainer .colorText {
    font-size: .9em;
    margin: 1px 3px 1px 1.7em;
    opacity: 0;
    -webkit-transition: .3s;
    transition: .3s;
    cursor: pointer;
}

.colorContainer:hover .colorText {
    opacity: 1;
}

.clipboard {
    border: none;
    background-color: transparent;
    height: auto;
    width: auto;
    border-radius: 5em;
}



.colorPreviewMini {
    height: 1.5em;
    width: 1.5em;
    margin: .25em;
    border-radius: 50%;
    -webkit-transition: .3s;
    transition: .3s;
}

.colorPreviewMiniSelect {
    height: 2em;
    width: 2em;
    border-radius: 50%;
    -webkit-transition: .3s;
    transition: .3s;
}

.colorPreviewBack {
    padding: 1em;
    position: relative;
    border-radius: 50%;
    cursor: pointer;
    width: 2em;
    height: 2em;
    -webkit-transition: .3s;
    transition: .3s;
}

.generalContainer {
    list-style: none;
    display: flex;
}
.previewContainer {
    padding: 1em;
    margin-left: 1em;
    margin-top: 3em;
}

.previewContainer a {
    margin: 1em;
}

.editorSample {
    -webkit-transition: .3s;
    transition: .3s;
}
.buttonColora {
    margin: 1em;
    padding: 1em 1.5em 1em 1.5em;
    position: absolute;
    width: auto;
    color: #9e9e9e;
    -webkit-transition: .3s;
    transition: .3s;
    cursor: pointer;
}

.buttonColora:hover {
    color: #212121;
}
